import React, { Component } from 'react'
import Header from '../layout/Header';
import Like from './like';
import RandomCard from './randomCard';

export class user extends Component {
    render() {
        return (
            <div>
                 <Header/>
                 <RandomCard userIndex={this.state.userIndex} usersList= {this.state.usersList}/>
                 <Like userIndex={this.props.userIndex}  nextUser={this.props.nextUser}/>
            </div>
        )
    }
}

export default user
