import Slider from '@material-ui/core/Slider';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import React, { Component } from 'react';
import Button from '@material-ui/core/Button';

   const useStyles = makeStyles({root: {width: 300,},});


export default class Age extends Component {
  render() {

    function Age()  {
 
  
   const classes = useStyles();
   const [Min_Age, setMinAge] = React.useState(18);
   const [Max_Age, setMaxAge] = React.useState(99);



   const MinAgeChange = (event, newValue) => {
  if (newValue > this.Max_Age || newValue < 18) {
    newValue = 18;
  }
  this.setMinAge(newValue);
};

 const MaxAgeChange = (event, newValue) => {
  if (newValue < this.Min_Age) {
    newValue = 99;
  }   
  this.setMaxAge(newValue);
};


  return (
    <div className={classes.root}>
      <Typography id="range-slider" gutterBottom ><h1>Show Ages</h1></Typography>
      <Slider value={Min_Age} onChange={MinAgeChange} valueLabelDisplay="on" aria-labelledby="range-slider"/>
      <Slider value={Max_Age} onChange={MaxAgeChange} valueLabelDisplay="on" aria-labelledby="range-slider" />
      <p> Min Age: {Min_Age} </p>
      <p> Max Age: {Max_Age} </p>
    </div>
  );

}
    return (
      <div>
      <Button onClick={this.props.filter_tinder(this.props.usersList,this.props.Min_Age,this.props.Max_Age)} variant="contained" color="primary">Submit </Button>

      </div>
    )
  }
}
